import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, useNavigate } from 'react-router-dom';
import { TextField, Button, Container, Typography, Card, CardContent, Select, MenuItem } from '@mui/material';
import axios from 'axios';
import { Client } from '@stomp/stompjs';
import SockJS from 'sockjs-client';
import { jwtDecode } from 'jwt-decode';

const API_URL = `${window.location.protocol}//${window.location.hostname}:${window.location.port}`;
const WS_URL = `${window.location.protocol}//${window.location.hostname}:${window.location.port}/ws`;

let stompClient = null;

function connectWS(onConnect) {
  const socket = new SockJS(`${WS_URL}?token=${sessionStorage.getItem('token')}`);
  stompClient = new Client({
    webSocketFactory: () => socket,
    reconnectDelay: 5000,
    onConnect: () => {
      console.log('Connected to WS');
      if (onConnect) onConnect(stompClient);
    },
    onStompError: (frame) => console.error('Broker error:', frame),
    onWebSocketError: (error) => console.error('WS error:', error),
  });
  stompClient.activate();
}

function Login() {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const navigate = useNavigate();

  const handleLogin = async () => {
    try {
      const response = await axios.post(`${API_URL}/auth/login`, { username, password });
      const token = response.data.token;

      sessionStorage.setItem('token', token);
      sessionStorage.setItem('username', username);

      const decoded = jwtDecode(token);
      sessionStorage.setItem('userId', decoded.userId);
      sessionStorage.setItem('role', decoded.tipoDeUsuario);

      if (decoded.tipoDeUsuario === 'AQV') navigate('/aqv');
      else if (decoded.tipoDeUsuario === 'PROFESSOR') navigate('/professor');
      else navigate('/aluno');
    } catch (err) {
      alert('Login falhou');
    }
  };

  const handleRegister = () => navigate('/register');

  return (
    <Container maxWidth="sm">
      <Typography variant="h4" gutterBottom>Login</Typography>
      <TextField label="Username" fullWidth margin="normal" value={username} onChange={e => setUsername(e.target.value)} />
      <TextField label="Password" type="password" fullWidth margin="normal" value={password} onChange={e => setPassword(e.target.value)} />
      <Button variant="contained" color="primary" onClick={handleLogin}>Login</Button>
      <Button variant="outlined" color="secondary" onClick={handleRegister} sx={{ mt: 2 }}>Registrar</Button>
    </Container>
  );
}

function Register() {
  const [nome, setNome] = useState('');
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [tipoDeUsuario, setTipoDeUsuario] = useState('ALUNO');
  const navigate = useNavigate();

  const handleRegister = async () => {
    try {
      await axios.post(`${API_URL}/auth/register`, { nome, username, password, tipoDeUsuario });
      alert('Usuário registrado com sucesso!');
      navigate('/');
    } catch (err) {
      alert('Falha ao registrar usuário');
    }
  };

  return (
    <Container maxWidth="sm">
      <Typography variant="h4" gutterBottom>Registrar</Typography>
      <TextField label="Nome" fullWidth margin="normal" value={nome} onChange={e => setNome(e.target.value)} />
      <TextField label="Username" fullWidth margin="normal" value={username} onChange={e => setUsername(e.target.value)} />
      <TextField label="Password" type="password" fullWidth margin="normal" value={password} onChange={e => setPassword(e.target.value)} />
      <Select fullWidth margin="normal" value={tipoDeUsuario} onChange={e => setTipoDeUsuario(e.target.value)}>
        <MenuItem value="ALUNO">Aluno</MenuItem>
        <MenuItem value="PROFESSOR">Professor</MenuItem>
        <MenuItem value="AQV">AQV</MenuItem>
      </Select>
      <Button variant="contained" color="primary" onClick={handleRegister}>Registrar</Button>
    </Container>
  );
}

function AlunoView() {
  const [motivo, setMotivo] = useState('');
  const [resposta, setResposta] = useState(null);
  const [professores, setProfessores] = useState([]);
  const [professorResponsavelId, setProfessorResponsavelId] = useState('');
  const alunoUsername = sessionStorage.getItem('username');
  const alunoId = sessionStorage.getItem('userId');

  useEffect(() => {
    connectWS((client) => {
      client.subscribe(`/user/queue/ocorrencia`, msg => {
        setResposta(JSON.parse(msg.body));
      });
    });

    axios.get(`${API_URL}/professores`).then(response => {
      setProfessores(response.data);
    });
  }, [alunoUsername]);

  const enviarPedido = () => {
    if (stompClient && stompClient.connected) {
      const ocorrencia = {
        tipo: 'SAIDA_ANTECIPADA',
        professorResponsavelId,
        descricao: motivo,
        alunoId
      };
      stompClient.publish({
        destination: '/app/ocorrencia/saida',
        body: JSON.stringify(ocorrencia)
      });
      alert('Pedido de saída enviado');
    }
  };

  return (
    <Container maxWidth="sm">
      <Typography variant="h5">Aluno - Pedido de Saída Antecipada</Typography>
      <TextField label="Motivo" fullWidth value={motivo} onChange={e => setMotivo(e.target.value)} />

      <Select
        fullWidth
        value={professorResponsavelId}
        onChange={e => setProfessorResponsavelId(e.target.value)}
        displayEmpty
        sx={{ mt: 2 }}
      >
        <MenuItem value="">Selecione um Professor</MenuItem>
        {professores.map(prof => (
          <MenuItem key={prof.id} value={prof.id}>{prof.nome}</MenuItem>
        ))}
      </Select>

      <Button variant="contained" onClick={enviarPedido} sx={{ mt: 2 }}>Enviar Pedido</Button>

      {resposta && (
        <Card sx={{ mt: 2 }}>
          <CardContent>
            <Typography>Status: {resposta.status}</Typography>
            <Typography>Descrição: {resposta.descricao}</Typography>
          </CardContent>
        </Card>
      )}
    </Container>
  );
}

function AqvView() {
  const [pedidos, setPedidos] = useState([]);
  const aqvUsername = sessionStorage.getItem('username');

  useEffect(() => {
    connectWS((client) => {
      client.subscribe(`/user/queue/ocorrencia`, msg => {
        const novaOcorrencia = JSON.parse(msg.body);
        axios.get(`${API_URL}/alunos/${novaOcorrencia.alunoId}/nome`).then(response => {
          novaOcorrencia.nomeAluno = response.data;
          setPedidos(prev => [...prev, novaOcorrencia]);
        });        
      });
    });
  }, [aqvUsername]);

  const aprovar = (ocorrencia) => {
    if (stompClient) {
      ocorrencia.status = 'AGUARDANDO_CIENCIA_PROFESSOR';
      stompClient.publish({
        destination: '/app/ocorrencia/decisao',
        body: JSON.stringify(ocorrencia)
      });
      alert('Ocorrência aprovada e enviada ao Professor');
    }
  };

  const reprovar = (ocorrencia) => {
    if (stompClient) {
      ocorrencia.status = 'REPROVADO';
      stompClient.publish({
        destination: '/app/ocorrencia/decisao',
        body: JSON.stringify(ocorrencia)
      });
      alert('Ocorrência reprovada');
    }
  };

  return (
    <Container maxWidth="sm">
      <Typography variant="h5">AQV - Pedidos Pendentes</Typography>
      {pedidos.map((p, idx) => (
        <Card key={idx} sx={{ my: 2 }}>
          <CardContent>
            <Typography>Aluno: {p.nomeAluno} ID: {p.alunoId}</Typography>
            <Typography>Descrição: {p.descricao}</Typography>
            <Button variant="contained" color="primary" onClick={() => aprovar(p)}>Aprovar</Button>
            <Button variant="outlined" color="error" onClick={() => reprovar(p)} sx={{ ml: 2 }}>Reprovar</Button>
          </CardContent>
        </Card>
      ))}
    </Container>
  );
}

function ProfessorView() {
  const [ocorrencias, setOcorrencias] = useState([]);
  const professorUsername = sessionStorage.getItem('username');

  useEffect(() => {
    connectWS((client) => {
      client.subscribe(`/user/queue/ocorrencia`, msg => {
        setOcorrencias(prev => [...prev, JSON.parse(msg.body)]);
      });
    });
  }, [professorUsername]);

  const darCiencia = (ocorrencia) => {
    if (stompClient) {
      stompClient.publish({
        destination: '/app/ocorrencia/ciencia',
        body: JSON.stringify(ocorrencia)
      });
      alert('Ciência confirmada e aluno notificado');
    }
  };

  return (
    <Container maxWidth="sm">
      <Typography variant="h5">Professor - Ocorrências Pendentes de Ciência</Typography>
      {ocorrencias.map((o, idx) => (
        <Card key={idx} sx={{ my: 2 }}>
          <CardContent>
            <Typography>Aluno : {o.nome}</Typography>
            <Typography>Descrição: {o.descricao}</Typography>
            <Button variant="contained" onClick={() => darCiencia(o)}>Dar Ciência</Button>
          </CardContent>
        </Card>
      ))}
    </Container>
  );
}

function LoginApp() {
  return (
    <Router>
      <Routes>
        <Route path="/" element={<Login />} />
        <Route path="/register" element={<Register />} />
        <Route path="/aluno" element={<AlunoView />} />
        <Route path="/aqv" element={<AqvView />} />
        <Route path="/professor" element={<ProfessorView />} />
      </Routes>
    </Router>
  );
}

export default LoginApp;
